<script scoped>
export default {
  data() {
    return {
      clockTimer: null,
      navTime: "",
    };
  },
  methods: {
    //
    clockTime() {
      let dt = new Date();
      let y = dt.getFullYear();
      let mt = dt.getMonth() + 1;
      let day = dt.getDate();
      let h = dt.getHours(); //
      let m = dt.getMinutes(); //
      let s = dt.getSeconds(); //
      let t =
        "" +
        y +
        "" +
        mt +
        "" +
        day +
        "" +
        h +
        "" +
        m +
        "" +
        s +
        "";
      return t;
    },
  },
  created() {
    //
    setInterval(() => {
      this.navTime = this.clockTime();
    }, 1000);
  },
};
</script>

